"use client";

import { useState, useEffect } from "react";
import { Users, Trash2, Search } from "lucide-react";

// Import de la police Montserrat depuis Google Fonts
import { Montserrat } from "next/font/google";

// Configuration de la police Montserrat
const montserrat = Montserrat({
  subsets: ["latin"],
  weight: "800", // ExtraBold
});

interface Client {
  id: number;
  firstName: string;
  lastName: string;
  email: string;
  createdAt: string;
}

interface Seller {
  id: number;
  firstName: string;
  lastName: string;
  email: string;
  createdAt: string;
  shopName: string;
}

export default function UsersPage() {
  // Données simulées pour les clients
  const [clients, setClients] = useState<Client[]>([
    {
      id: 1,
      firstName: "Ahmed",
      lastName: "Ben Salah",
      email: "ahmed.bensalah@example.com",
      createdAt: "2024-01-15",
    },
    {
      id: 2,
      firstName: "Fatima",
      lastName: "Zohra",
      email: "fatima.zohra@example.com",
      createdAt: "2024-03-22",
    },
    {
      id: 3,
      firstName: "Youssef",
      lastName: "Khalil",
      email: "youssef.khalil@example.com",
      createdAt: "2024-05-10",
    },
  ]);

  // Données simulées pour les vendeurs
  const [sellers, setSellers] = useState<Seller[]>([
    {
      id: 1,
      firstName: "Karim",
      lastName: "Haddad",
      email: "karim.haddad@example.com",
      createdAt: "2023-11-05",
      shopName: "Karim Fashion",
    },
    {
      id: 2,
      firstName: "Leila",
      lastName: "Mansour",
      email: "leila.mansour@example.com",
      createdAt: "2024-02-18",
      shopName: "Leila Boutique",
    },
  ]);

  // États pour la recherche
  const [clientSearch, setClientSearch] = useState("");
  const [sellerSearch, setSellerSearch] = useState("");

  // États pour la suppression
  const [showDeleteModal, setShowDeleteModal] = useState(false);
  const [userToDelete, setUserToDelete] = useState<{
    id: number;
    type: "client" | "seller";
  } | null>(null);
  const [successMessage, setSuccessMessage] = useState<string | null>(null);

  // Filtrage des clients
  const filteredClients = clients.filter(
    (client) =>
      client.firstName.toLowerCase().includes(clientSearch.toLowerCase()) ||
      client.lastName.toLowerCase().includes(clientSearch.toLowerCase())
  );

  // Filtrage des vendeurs
  const filteredSellers = sellers.filter(
    (seller) =>
      seller.firstName.toLowerCase().includes(sellerSearch.toLowerCase()) ||
      seller.lastName.toLowerCase().includes(sellerSearch.toLowerCase()) ||
      seller.shopName.toLowerCase().includes(sellerSearch.toLowerCase())
  );

  // Gestion de la suppression
  const handleDeleteUser = (id: number, type: "client" | "seller") => {
    setUserToDelete({ id, type });
    setShowDeleteModal(true);
  };

  const confirmDelete = () => {
    if (userToDelete) {
      if (userToDelete.type === "client") {
        setClients(clients.filter((client) => client.id !== userToDelete.id));
        setSuccessMessage("Client supprimé avec succès !");
      } else {
        setSellers(sellers.filter((seller) => seller.id !== userToDelete.id));
        setSuccessMessage("Vendeur supprimé avec succès !");
      }
      setShowDeleteModal(false);
      setUserToDelete(null);
    }
  };

  const cancelDelete = () => {
    setShowDeleteModal(false);
    setUserToDelete(null);
  };

  // Effacer le message de succès après 3 secondes
  useEffect(() => {
    if (successMessage) {
      const timer = setTimeout(() => {
        setSuccessMessage(null);
      }, 3000);
      return () => clearTimeout(timer);
    }
  }, [successMessage]);

  return (
    <div className="min-h-screen bg-gradient-to-br from-gray-50 to-gray-200 py-12 px-4 sm:px-6 lg:px-8">
      <div className="max-w-7xl mx-auto">
        {/* Message de succès (Toast) */}
        {successMessage && (
          <div className="fixed top-4 right-4 z-50 animate-slide-in">
            <div className="bg-green-500 text-white px-6 py-3 rounded-lg shadow-lg flex items-center gap-2">
              <span>{successMessage}</span>
              <button
                onClick={() => setSuccessMessage(null)}
                className="text-white hover:text-gray-200"
              >
                ✕
              </button>
            </div>
          </div>
        )}

        {/* Titre de la page */}
        <div className="mb-10 flex items-center gap-3">
          <Users className="h-8 w-8 text-black" />
          <h1
            className={`text-4xl font-extrabold text-gray-900 uppercase tracking-tight ${montserrat.className}`}
          >
            Gestion des utilisateurs
          </h1>
        </div>

        {/* Section Clients */}
        <div className="mb-12">
          <h2 className="text-2xl font-bold text-gray-900 mb-6">Clients</h2>

          {/* Barre de recherche pour les clients */}
          <div className="mb-6 flex items-center gap-3">
            <div className="relative w-full max-w-md">
              <input
                type="text"
                value={clientSearch}
                onChange={(e) => setClientSearch(e.target.value)}
                placeholder="Rechercher par nom ou prénom..."
                className="w-full px-4 py-3 bg-white/50 border border-gray-200 rounded-lg focus:ring-2 focus:ring-gray-400 focus:border-gray-400 transition-all duration-200 text-gray-800 placeholder-gray-400"
              />
              <Search className="absolute right-3 top-1/2 transform -translate-y-1/2 h-5 w-5 text-gray-400" />
            </div>
          </div>

          {/* Tableau des clients */}
          <div className="bg-white/30 backdrop-blur-lg border border-gray-200/50 rounded-xl shadow-lg overflow-x-auto">
            <table className="min-w-full divide-y divide-gray-200">
              <thead className="bg-gray-50/50">
                <tr>
                  <th className="px-6 py-3 text-left text-sm font-bold text-gray-800">Nom</th>
                  <th className="px-6 py-3 text-left text-sm font-bold text-gray-800">Prénom</th>
                  <th className="px-6 py-3 text-left text-sm font-bold text-gray-800">Email</th>
                  <th className="px-6 py-3 text-left text-sm font-bold text-gray-800">Date de création</th>
                  <th className="px-6 py-3 text-left text-sm font-bold text-gray-800">Actions</th>
                </tr>
              </thead>
              <tbody className="divide-y divide-gray-200">
                {filteredClients.length > 0 ? (
                  filteredClients.map((client) => (
                    <tr key={client.id} className="hover:bg-gray-50/50 transition-all duration-200">
                      <td className="px-6 py-4 text-sm text-gray-800">{client.lastName}</td>
                      <td className="px-6 py-4 text-sm text-gray-800">{client.firstName}</td>
                      <td className="px-6 py-4 text-sm text-gray-800">{client.email}</td>
                      <td className="px-6 py-4 text-sm text-gray-800">{client.createdAt}</td>
                      <td className="px-6 py-4">
                        <button
                          onClick={() => handleDeleteUser(client.id, "client")}
                          className="bg-red-500 text-white px-4 py-2 rounded-lg flex items-center gap-2 hover:bg-red-600 transition-all duration-200 shadow-md"
                        >
                          <Trash2 className="h-4 w-4" /> Supprimer
                        </button>
                      </td>
                    </tr>
                  ))
                ) : (
                  <tr>
                    <td colSpan={5} className="px-6 py-4 text-center text-sm text-gray-500">
                      Aucun client trouvé.
                    </td>
                  </tr>
                )}
              </tbody>
            </table>
          </div>
        </div>

        {/* Section Vendeurs */}
        <div>
          <h2 className="text-2xl font-bold text-gray-900 mb-6">Vendeurs</h2>

          {/* Barre de recherche pour les vendeurs */}
          <div className="mb-6 flex items-center gap-3">
            <div className="relative w-full max-w-md">
              <input
                type="text"
                value={sellerSearch}
                onChange={(e) => setSellerSearch(e.target.value)}
                placeholder="Rechercher par nom, prénom ou nom de boutique..."
                className="w-full px-4 py-3 bg-white/50 border border-gray-200 rounded-lg focus:ring-2 focus:ring-gray-400 focus:border-gray-400 transition-all duration-200 text-gray-800 placeholder-gray-400"
              />
              <Search className="absolute right-3 top-1/2 transform -translate-y-1/2 h-5 w-5 text-gray-400" />
            </div>
          </div>

          {/* Tableau des vendeurs */}
          <div className="bg-white/30 backdrop-blur-lg border border-gray-200/50 rounded-xl shadow-lg overflow-x-auto">
            <table className="min-w-full divide-y divide-gray-200">
              <thead className="bg-gray-50/50">
                <tr>
                  <th className="px-6 py-3 text-left text-sm font-bold text-gray-800">Nom</th>
                  <th className="px-6 py-3 text-left text-sm font-bold text-gray-800">Prénom</th>
                  <th className="px-6 py-3 text-left text-sm font-bold text-gray-800">Email</th>
                  <th className="px-6 py-3 text-left text-sm font-bold text-gray-800">Date de création</th>
                  <th className="px-6 py-3 text-left text-sm font-bold text-gray-800">Nom de boutique</th>
                  <th className="px-6 py-3 text-left text-sm font-bold text-gray-800">Actions</th>
                </tr>
              </thead>
              <tbody className="divide-y divide-gray-200">
                {filteredSellers.length > 0 ? (
                  filteredSellers.map((seller) => (
                    <tr key={seller.id} className="hover:bg-gray-50/50 transition-all duration-200">
                      <td className="px-6 py-4 text-sm text-gray-800">{seller.lastName}</td>
                      <td className="px-6 py-4 text-sm text-gray-800">{seller.firstName}</td>
                      <td className="px-6 py-4 text-sm text-gray-800">{seller.email}</td>
                      <td className="px-6 py-4 text-sm text-gray-800">{seller.createdAt}</td>
                      <td className="px-6 py-4 text-sm text-gray-800">{seller.shopName}</td>
                      <td className="px-6 py-4">
                        <button
                          onClick={() => handleDeleteUser(seller.id, "seller")}
                          className="bg-red-500 text-white px-4 py-2 rounded-lg flex items-center gap-2 hover:bg-red-600 transition-all duration-200 shadow-md"
                        >
                          <Trash2 className="h-4 w-4" /> Supprimer
                        </button>
                      </td>
                    </tr>
                  ))
                ) : (
                  <tr>
                    <td colSpan={6} className="px-6 py-4 text-center text-sm text-gray-500">
                      Aucun vendeur trouvé.
                    </td>
                  </tr>
                )}
              </tbody>
            </table>
          </div>
        </div>

        {/* Modale de confirmation pour la suppression */}
        {showDeleteModal && (
          <div className="fixed inset-0 bg-black/60 flex items-center justify-center z-50 animate-fade-in">
            <div className="bg-white/30 backdrop-blur-lg border border-gray-200/50 p-6 rounded-xl shadow-2xl max-w-sm w-full">
              <h3 className="text-xl font-semibold text-gray-900 mb-4">
                Confirmer la suppression
              </h3>
              <p className="text-gray-600 mb-6">
                Êtes-vous sûr de vouloir supprimer cet utilisateur ? Cette action est irréversible.
              </p>
              <div className="flex justify-end gap-3">
                <button
                  onClick={cancelDelete}
                  className="bg-gray-200 text-gray-800 px-4 py-2 rounded-lg font-semibold hover:bg-gray-300 transition-all duration-300"
                >
                  Annuler
                </button>
                <button
                  onClick={confirmDelete}
                  className="bg-red-500 text-white px-4 py-2 rounded-lg font-semibold hover:bg-red-600 transition-all duration-300"
                >
                  Confirmer
                </button>
              </div>
            </div>
          </div>
        )}
      </div>
    </div>
  );
}